<template>
  <div class="container my-5 px-4 px-lg-5">
    <div class="row gx-4 gx-lg-5 justify-content-center">
      <div class="col-md-10 col-lg-8 col-xl-7">
        <h2></h2>
        <div class="my-5">
          <form id="userform" method="post" action="">
            <div class="text-start">
              <label for="name"></label>
              <input
                class="form-control"
                id="name"
                type="text"
                v-model="name"
                placeholder=" ."
              />
            </div>
            <div class="text-start">
              <label for="id">ID</label>
              <input
                class="form-control"
                id="id"
                type="text"
                v-model="id"
                placeholder="ID ."
              />
              <div id="idresult" class="mt-1"></div>
            </div>
            <div class="text-start">
              <label for="pass">Password</label>
              <input
                class="form-control"
                id="pass"
                type="password"
                v-model="pass"
                placeholder=" ."
              />
            </div>
            <div class="text-start">
              <label for="pass">Password Check</label>
              <input
                class="form-control"
                id="passcheck"
                type="password"
                v-model="passcheck"
                placeholder="  ."
              />
            </div>
            <!-- email  -->
            <div class="text-start">
              <p class="mt-3 mb-0">()</p>
              <label for="email">E-mail</label>
              <input
                class="form-control"
                id="email"
                type="e-mail"
                v-model="email"
                placeholder="e-mail .(example: -----@-----.---  )"
              />
            </div>
            <!--  -->
            <div class="text-start">
              <label for="genre"> </label>
              <input
                class="form-control"
                id="genre"
                type="text"
                v-model="genre"
                placeholder="  ."
              />
            </div>
            <!--  -->
             <div class="text-start">
              <label for="goal"></label>
              <input
                class="form-control"
                id="goal"
                type="text"
                v-model="goal"
                placeholder=" ."
              />
            </div>           
            <!--   -->
             <div class="text-start">
              <label for="socialLink">SNS </label>
              <input
                class="form-control"
                id="socialLink"
                type="url"
                v-model="socialLink"
                placeholder=" .(example: https://----.---  )"
              />
            </div>    
            <br />
            <!-- Submit Button-->
            <div class="float-end">
              <b-button variant="primary" @click="checkValue"
                ></b-button
              >&nbsp;
              <b-button @click="resetValue"
                ></b-button
              >
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/config/http-common.js";
import Swal from "sweetalert2";

export default {
  name: "Signup",
  data() {
    return {
      name: "",
      id: "",
      pass: "",
      passcheck: "",
      email: "",
      genre: null,
      goal: null,
      socialLink: ""
    };
  },

  methods: {
    checkValue: function () {
      if (
        this.name === "" ||
        this.pass === "" ||
        this.passcheck === "" ||
        this.id == ""
      ) {
        Swal.fire({
          icon: "error",
          title: "Stop!",
          text: ", , ,    .",
        });
      } else if (this.pass !== this.passcheck) {
        Swal.fire({
          icon: "error",
          title: "PasswordNotMatch",
          text: "  ",
        });
      } else if (this.name.length > 30) {
        Swal.fire({
          icon: "error",
          title: "UsernameError",
          text: " 30   .",
        });
      } else if (this.id.length > 16) {
        Swal.fire({
          icon: "error",
          title: "IdError",
          text: " 16    ",
        });
      } else if (
        16 < this.pass.length ||
        9 > this.pass.length
      ) {
        Swal.fire({
          icon: "error",
          title: "  ",
          text: "() 9  16  .",
        });
      } else if (
        this.pass.search(/[0-9]/g) < 0 ||
        this.pass.search(/[a-z]/gi) < 0 ||
        this.pass.search(/[`~!@#$%^&*/?;:]/gi) < 0
      ) {
        Swal.fire({
          icon: "error",
          title: "  ",
          text: " , ,   .",
        });
      } else {
        Swal.fire({
        icon: "question",
        text: '  ?',
        showCancelButton: true,
        confirmButtonText: '',
        }).then((result) => {
          if (result.isConfirmed) {
            this.registMember();
          } else {
            Swal.fire({
              icon: "warning",
              text: ".",
            });
          }
        });
      }
    },
    //      
    createMarathon(id) {
      http
        .post(`/marathon/create`, {
          userId: id,
          goalPages: 1 //  1 
        })
        .then(({ data }) => {
          //      .
          console.log(data)
        })
        .catch((err) => {
          console.log(err.response)
          Swal.fire({
            icon: "error",
            text: "  .  .",
          })
        })
    },
    registMember() {
      http
        .post(`/users`, {
          userName: this.name,
          userId: this.id,
          password: this.pass,
          email: this.email,
          genre: this.genre,
          goal: this.goal,
          socialLink: this.socialLink
        })
        .then(({ data }) => {
          let msg = "  .";
          //      .
          if (data === "success") {
            msg = " .";
            //  
            this.createMarathon(this.id)
            Swal.fire({
              icon: "success",
              text: msg,
            })
            this.$router.push({ name: "Home" });
          }
        })
        .catch((err) => {
          Swal.fire({
            icon: "error",
            text: err.response.data,
        })
      })
    },
    resetValue() {
      this.$router.push("/");
    },
  },
};
</script>

