<template>
  <div>
    <b-form-select v-model="selected" :options="options" class="mb-3" style="height: 2.5rem">
      <template #first>
        <b-form-select-option :value="null" disabled>-- --</b-form-select-option>
      </template>
    </b-form-select>
    <b-button class="ms-2" @click="getFilter"></b-button>

    <!-- <div class="mt-3">Selected: <strong>{{ selected }}</strong></div> -->
  </div>

</template>

<script>
import { mapActions } from "vuex";
import Swal from "sweetalert2";

const conferenceStore = "conferenceStore";

export default {
  name: 'ConferenceFilter',
  methods: {
    ...mapActions(conferenceStore, ['getConferenceByCategory']),
    getFilter() {
      // console.log(this.selected)
      if (this.selected == null) {
        // alert(' ')
        Swal.fire({
          icon: "error",
          text: " .",
        })
      } else {
        this.getConferenceByCategory(this.selected)
      }
  }
  },
  data: function () {
    return {
      selected: null,
      options: [
        { value: "1", text: " " },
        { value: "2", text: "" },
        { value: "3", text: "" },
      ]
    }
  },

}
</script>

