<template>
  <div class="container text-center">
    <h2 class="fw-bold my-5 text-start"></h2>
    <!-- <h2 class="fw-bold my-5 text-start">Profile</h2> -->
    
    <vue-profile 
    :nickname="`${userInfo.userName}`"
    :bodyPhrase="` : ${userInfo.genre} & : ${userInfo.goal}`"
    :socialLinks="myLinks"
    :profileImg="Img"
    coverColor="#5498e9"
    /> 
    <!--   -->
    
    <b-button v-b-modal.modal2-prevent-closing class="me-3" @click="getUserData()"> </b-button>
      <b-modal
        id="modal2-prevent-closing"
        ref="modal"
        title=" "
        hide-footer
        v-if="modalCheck"
      > 
      
      <!-- <b-button v-b-modal.modalPopover sm class="me-3">  </b-button>
      <b-modal id="modalPopover" size="sm" title="  " ok-only>
      <b-form-group label="Radios using sub-components" v-slot="{ ariaDescribedby }">
        <b-form-radio-group
          id="radio-group-2"
          v-model="selected"
          :aria-describedby="ariaDescribedby"
          name=""
        >
          <b-form-radio value="1"><img src="@/assets/profile/1.jpg" alt="img_1" width="100px"></b-form-radio>
          <b-form-radio value="2"><img src="@/assets/profile/2.jpg" alt="img_2" width="100px"></b-form-radio>
          <b-form-radio value="3"><img src="@/assets/profile/3.jpg" alt="img_3" width="100px"></b-form-radio>
          <b-form-radio value="4"><img src="@/assets/profile/4.jpg" alt="img_4" width="100px"></b-form-radio>
          <b-form-radio value="5"><img src="@/assets/profile/5.jpg" alt="img_5" width="100px"></b-form-radio>
        </b-form-radio-group>
      </b-form-group>
      <div class="mt-3">Selected: <strong>{{ selected }}</strong></div>
      </b-modal> -->
      
        <!-- <form ref="form">
          <b-form-group
            type="file"
            label="  "
            label-for="profileImg"
          >
            <b-form-file
              multiple
              type="file"
              id="profileImg"
              accept=".png, .jpg, .jpeg, .gif"
              v-model="user.profileImage"
              plain
            ></b-form-file>
            <div class="mt-3">Selected file: {{ user.profileImage ? user.profileImage : '' }}</div>
          </b-form-group>
        </form> -->

        <form ref="form">
          <b-form-group
            label=" username"
            label-for="username"
          >
            <b-form-input
              id="username"
              v-model="user.user_name"
              placeholder=" username "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label=" "
            label-for="user_pw"
          >
            <b-form-input
              type="password"
              id="user_pw"
              v-model="user.user_pw"
              placeholder=" password "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="  "
            label-for="user_pw_check"
          >
            <b-form-input
              type="password"
              id="user_pw_check"
              v-model="user.user_pw_check"
              placeholder=" password "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label=" email"
            label-for="user_email"
          >
            <b-form-input
              type="email"
              id="user_email"
              v-model="user.email"
              placeholder=" e-mail (-----@-----.--- )"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="  "
            label-for="user_genre"
          >
            <b-form-input
              type="text"
              id="user_genre"
              v-model="user.genre"
              placeholder="   "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="  "
            label-for="user_goal"
          >
            <b-form-input
              type="text"
              id="user_goal"
              v-model="user.goal"
              placeholder="  "
            ></b-form-input>
          </b-form-group>
        </form>
        <form>
          <b-form-group
            label=" SNS "
            label-for="user_socialLink"
          >
            <b-form-input
              type="url"
              id="user_socialLink"
              v-model="user.socialLink"
              placeholder=" SNS (https://----.--- )"
              @keyup.enter="checkValue()"
            ></b-form-input>
          </b-form-group>
        </form>
        <hr />
        <b-button class="float-end" variant="outline-primary" @click="checkValue()">  </b-button>
      </b-modal>
    <b-button @click="deleteConfirm"></b-button>


    <!--    -->
    <b-row class="justify-content-center mt-5" v-if="marathon.max">  <!-- max value  (    )  -->
      <p class="fs-3 fw-bold">
        <i class="fas fa-running" style="color: #0dcaf0;"></i>
           
        <!-- <i class="fas fa-running" style="color: #0dcaf0;"></i> -->
      </p> <!--   -->
      <b-progress
        :max="marathon.max"
        show-value
        class="mb-3 col-4 px-0"
        animated
        height="1.5rem"
      >
        <b-progress-bar :value="marathon.value" :variant=color()>
          <span><strong>{{ marathon.value }} / {{ marathon.max }} km</strong></span>
        </b-progress-bar>
      </b-progress>
      
      <p v-if="marathon.value !== 0 && marathon.value >= marathon.max">
         100% !  .
        <b-button v-b-modal.modal-sm size="sm" variant="warning" class="ms-2" @click="marathon.modalCheck=true">!</b-button>
        <b-modal v-if="marathon.modalCheck" hide-footer hide-header id="modal-sm" size="sm" title="  ">
          <b-input-group prepend="km" class="mt-3">
          <b-form-input :value="marathon.max" v-model="changeValue" @keyup.enter="updateGoal();"></b-form-input>
          <b-input-group-append>
            <b-button variant="outline-warning" @click="updateGoal();">!</b-button>
          </b-input-group-append>
          </b-input-group>
        </b-modal>
      </p>
      <h5 v-else>
         <span class="text-warning">{{ marathon.max -  marathon.value }}km!</span>
      </h5>
      <p v-if="marathon.value===0">
            up!
      </p>
      <bookmark></bookmark>
    </b-row>

       

  </div>
  
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
import { mapActions } from "vuex";
import http from "@/config/http-common.js";
import Swal from "sweetalert2";
import Bookmark from './Bookmark'


const userStore = "userStore";
export default {
  components: {
    Bookmark,
  },
  data() {
    return {
      myLinks: {         
        email: "",
        link: "", 
      },
      Img: require('@/assets/profile/profile_default_img.jpg'),
      marathon: {
        id: 0,
        value: 0, 
        max: 0,
        modalCheck: false
      },
      user: {
        profileImage: "",
        user_name: "",
        user_pw: "",
        user_pw_check: "",
        email: "",
        genre: "",
        goal: "",
        socialLink: "",
      },
      modalCheck: false,
      changeValue: 0,
      selected: null,
    };
  },
  methods: {
    ...mapMutations(userStore, ["SET_IS_LOGIN", "SET_USER_INFO"]),
    ...mapActions(userStore, ["getUserInfo"]),
    //  
    deleteConfirm() {
      Swal.fire({
        icon: "question",
        text: '?',
        showCancelButton: true,
        confirmButtonText: '',
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteUser()
        } else {
          Swal.fire({
            icon: "info",
            text: " .",
          });
        }
      })
    },
    deleteUser() {
      // 
      http({
      method: 'delete',
      url: `/users/${this.userInfo.userId}`,
      })
      .then((response) => {
        console.log(response);
        // 
        this.SET_IS_LOGIN(false);
        this.SET_USER_INFO(null);
        this.$router.push({
          path: "/",
        })
        // alert(' ')
        Swal.fire({
          icon: "info",
          text: " .",
        });
      })
      .catch((err) => {
        console.dir(err);
      })
    },
    getUserData() { //   
      this.modalCheck = true
      this.user.user_name = this.userInfo.userName
      this.user.email = this.userInfo.email
      this.user.genre = this.userInfo.genre
      this.user.goal = this.userInfo.goal
      this.user.socialLink = this.userInfo.socialLink
      this.user.profileImage = this.userInfo.profileImage
      
    },
    checkValue: function () {
      if (
        this.user.user_pw === "" ||
        this.user.user_pw_check === "" ||
        this.user.user_name === ""
      ) {
        Swal.fire({
          icon: "error",
          title: "Stop!",
          text: ", ,    .",
        });
      } else if (this.user.user_pw !== this.user.user_pw_check) {
        Swal.fire({
          icon: "error",
          title: "PasswordNotMatch",
          text: "  ",
        });
      } else if (this.user.user_name.length > 30) {
        Swal.fire({
          icon: "error",
          title: "UsernameError",
          text: "username 30   .",
        });
      } else if (
        16 < this.user.user_pw.length ||
        9 > this.user.user_pw.length
      ) {
        Swal.fire({
          icon: "error",
          title: "  ",
          text: "() 9  16  .",
        });
      } else if (
        this.user.user_pw.search(/[0-9]/g) < 0 ||
        this.user.user_pw.search(/[a-z]/gi) < 0 ||
        this.user.user_pw.search(/[`~!@#$%^&*/?;:]/gi) < 0
      ) {
        Swal.fire({
          icon: "error",
          title: "  ",
          text: " , ,   .",
        });
      } else if (
        this.user.user_pw.search(/[0-9]/g) < 0 ||
        this.user.user_pw.search(/[a-z]/gi) < 0 ||
        this.user.user_pw.search(/[`~!@#$%^&*/?;:]/gi) < 0
      ) {
        Swal.fire({
          icon: "error",
          title: "  ",
          text: " , ,   .",
        });
      } else if (
        this.user.user_name === this.userInfo.userName 
      ) {
        Swal.fire({
          icon: "question",
          text: 'username  ?',
          showCancelButton: true,
          confirmButtonText: '',
        }).then((result) => {
          if (result.isConfirmed) {
            this.userInfoUpdate()
          }
        });
      } else {
          this.userInfoUpdate()
          // Swal.fire('  .', '', 'success')
      }
    },
    userInfoUpdate() { //  
      console.log(this.user.profileImage)
      http({
        headers: { 'Content-Type': 'multipart/form-data' }, //  
        method: 'put',
        url: `/users/${this.userInfo.userId}`,
        data: {
          'password': this.user.user_pw,
          'userId': this.userInfo.userId,
          'userName': this.user.user_name,
          'email': this.user.email,
          'genre': this.user.genre,
          'goal': this.user.goal,
          'socialLink': this.user.socialLink,
          'profileImage': this.user.profileImage //  
          }
        })
        .then((response) => {
          console.log(response)
          // alert('  ')
          Swal.fire({
            icon: "success",
            text: "  .",
          });

          //         ( userStore )
          let token = sessionStorage.getItem("access-token");
          this.getUserInfo(token)
          this.myLinks.email = this.userInfo.email, //  
          this.myLinks.link = this.userInfo.socialLink //  
          
          //       
          this.user.user_pw = ""
          this.user.user_pw_check = ""
          this.modalCheck = false
          
        })
        .catch((err) => {
          console.dir(err)
        })
    },
    //  
    getMarthonInfo() {
      http({
        method: "get",
        url: `/marathon/marathon-info/${this.userInfo.userId}`,
      })
        .then((response) => {
          console.log(response.data)
          this.marathon.value = response.data.nowPages
          this.marathon.max = response.data.goalPages
          this.marathon.id = response.data.id
          this.changeValue = response.data.nowPages
        })
        .catch((error) => {
          console.log(error)
          Swal.fire({
            icon: "info",
            text: "    .  .",
          })
        })
    },
    //  (goalPages)  -->   
    updateGoal(){
       http({
        method: "put", // ...? //   (  /  max /  value /  userid /  max)                      //  p ???
        url: `/marathon/updateGoalpages?goalPages=${this.marathon.max}&id=${this.marathon.id}&nowPages=${this.marathon.value}&userId=${this.userInfo.userId}&goalpages=${this.changeValue}`,
        //    /marathon/updateGoalpages?goalPages=100                 &id=1                  &nowPages=1                     &userId=test                   &goalpages=2
      })
        .then((response) => {
          console.log(response)
          // alert(' !')
          Swal.fire({
            icon: "success",
            text: " !",
          })
          this.marathon.modalCheck = false
          this.getMarthonInfo()
        })
        .catch((error) => {
          console.log(error)
          this.marathon.modalCheck = false
          // alert(' .  ')
          Swal.fire({
            icon: "error",
            text: " .  .",
          })
        });
    },
    color() { //     ()
      if (this.marathon.value >= this.marathon.max) {
        return "danger"
      } else {
        return "primary"
      }
    }
  },
  created() {
    //  
    this.myLinks.email = this.userInfo.email,
    this.myLinks.link = this.userInfo.socialLink
    //    
    this.getMarthonInfo()
    
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
  },
};
</script>

