<template>
  <div class="text-center">
    <b-button
      variant="primary"
      @click="$bvModal.show(item.isbn13); selectBook(item)"
      > </b-button
    >
    
    <b-modal scrollable :id=item.isbn13 ref="modal" title=" " v-if="userInfo">
      <h5 class="d-inline"> : {{ selectedBook }}</h5>
      <hr>
      <form ref="form">
        <b-form-group
          class="roommodal"
          id="dropdown-1"
          label=""
          label-for="usage-roominput"
        >
          <b-form-select v-model="selectedOption" :options="options" style="width:100%; height:2.5rem; border-color:#ced4da;">
            <template #first>
              <b-form-select-option :value="null" disabled
                >-- --</b-form-select-option
              >
            </template>
          </b-form-select>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          class="roommodal"
          label=" "
          label-for="title"
        >
          <b-form-input
            id="title"
            v-model="roomName"
            placeholder="  "
          ></b-form-input>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          type="textarea"
          class="roommodal"
          label=""
          label-for="description"
        >
          <b-form-textarea
            id="description"
            v-model="roomContent"
            placeholder=" "
            row="3"
            max-rows="6"
          ></b-form-textarea>
        </b-form-group>
      </form>
      <br />
      <template #modal-footer="{ ok, cancel }">
        <b-button
          variant="outline-primary"
          class="me-2"
          @click="roomCreate() ? ok() : _"
        >
          
        </b-button>
        <b-button
          @click="
            formReset();
            cancel();
          "
        >
          
        </b-button>
      </template>
    </b-modal>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Swal from "sweetalert2";
import http from "@/config/http-common.js";


export default {
  name: "BookConferenceCreate",
  data() {
    return {
      roomName: "",
      roomContent: "",
      bookSearchValue: "",
      selectedOption: null, // ( )             
      options: [ //   DB        //      
        { value: "1", text: " " },
        { value: "2", text: "" },
        { value: "3", text: "" },
      ],
      bookData: 1,
      selectedBook: '',
      bookDetailId: null
    };
  },
  props: {
    item: Object,
  },
  computed: {
    ...mapState("userStore", ["userInfo"]),
  },
  methods: {
    ...mapActions("conferenceStore", ["createRoom"]),
    roomIsValid: function () {
      if (this.selectedOption === null || this.roomName === "") {
        Swal.fire({
          icon: "error",
          title: "Stop!",
          text: ",   .",
        });
        return false;
      } else if (this.roomName.length > 31) {
        Swal.fire({
          icon: "error",
          title: "RoomnameError",
          text: "  30  .",
        });
        return false;
      } else {
        return true;
      }
    },
    roomData() {
      return {
        user: {
          userId: this.userInfo.userId, 
        },
        bookDetail: {
          id: this.bookDetailId, 
        }, //  - book DB  
        conferenceType: { 
          id: this.selectedOption,
        },
        title: this.roomName,
        description: this.roomContent,
        isActive: 1
      };
    },
    formReset() {
      this.selectedOption = null;
      this.thumbnailFile = "";
      this.roomName = "";
      this.roomContent = "";
      this.roomSearchValue = "";
      // this.bookData = 1 ;
    }, //  form 
    roomCreate() {
      if (this.roomIsValid()) {
        this.createRoom(this.roomData()); //   //   payload 
        this.formReset(); //  form 
        return true;
      } else {
        return false;
      }
    },
    bookSearch() {
      //  
      if (this.bookSearchValue !== "") {
        http({
          method: "get",
          url: `/search/naver?query=${this.bookSearchValue}`,
        })
          .then((response) => {
            console.log(response);
            this.bookData = response.data.items
          })
          .catch((error) => {
            console.dir(error);
          });
      } else {
        Swal.fire({
          icon: "error",
          text: " .",
        })
      }
    },
    transStr(str) {
      // return str
      var transedStr = str.replaceAll('<b>', '')
      transedStr = transedStr.replaceAll('</b>', '')
      return transedStr
    },
    // isbn  pk 
    checkBookInDB(book) {
       console.log(book)
       const isbn = book.isbn13
       console.log(isbn)
       http({
        method: "get",
        url: `/search/${isbn}`,
      })
        .then((response) => {
          console.log(response);
          this.bookDetailId = response.data
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    selectBook(book) {
      if (this.userInfo) {
        this.selectedBook = this.transStr(book.title)
        // this.bookData = 2
        this.bookDetail.id = this.checkBookInDB(book)
      } else {
        Swal.fire({
          icon: "warning",
          text: "  !",
        })
      }

    }
  },
};
</script>

