<template>
  <b-col>
    <b-button @click="closeConference()"> </b-button> 
  </b-col>
</template>

<script>
import { mapActions } from "vuex";
import Swal from "sweetalert2";

const conferenceStore = "conferenceStore";

export default {
  name: "ConferenceDetailClose",
  computed: {
  },
  data() {
    return {
        conferenceId: this.$route.params.contentId,
      }
  },
  methods: {
    ...mapActions(conferenceStore, ["conferenceClose"]),
    closeConference() {
      Swal.fire({
        icon: "question",
        text: ' ?',
        showCancelButton: true,
        confirmButtonText: '',
      }).then((result) => {
        if (result.isConfirmed) {
          this.conferenceClose(this.conferenceId)
        } else {
          Swal.fire({
            icon: "warning",
            text: ".",
          });
        }
      });
    }
  },
};
</script>

