<template>
  <div class="col" v-if="item.isActive || !checked"> <!--    -->
    <b-card no-body bg-variant="dark" class="overflow-hidden shadow">
      <b-row no-gutters>
        <b-col md="6">
          <b-card-img v-if="transUrl(item.bookDetail.titleUrl)" :src="transUrl(item.bookDetail.titleUrl)" @error="replaceByDefault" alt="Image" class="rounded-3 mt-3 ms-3"></b-card-img>
          <b-card-img v-else :src="thumbnail_url" alt="book_image" class="rounded-3 mt-3 ms-3"></b-card-img>
        </b-col>
        <b-col md="6" class="mb-3">
          <b-card-body :title="item.title" class="my-2"> <!-- -->
          <hr class="my-2">
            <b-card-text>
              <!-- <p class="card-text">: {{ item.id }}</p> -->
              <p class="card-text my-2">: {{ transStr(item.bookDetail.title) }}</p>
              <hr class="my-2">
              <p class="card-text">: {{ item.conferenceType.name }}</p> 
              <p class="card-text">: {{ item.callStartTime }}</p>
              <!-- <p class="card-text">: {{ item.callEndTime }}</p> -->
              <p v-if="item.isActive" class="card-text"> : <span class="text-success"></span> </p> 
              <p v-else class="card-text"> : <span class="text-danger"></span></p> 
              <!-- <p class="card-text">  : {{ item.participant.length }}</p> participant array  -->
              
            </b-card-text>
          </b-card-body>
          <b-button variant="primary" @click="enterConference" v-if="item.isActive" class="float-end me-2"></b-button> 
          <b-button variant="secondary" class="disabled float-end me-2" v-else></b-button>
        </b-col>
      </b-row>
    </b-card>
  </div>


</template>

<script>
import { mapState, mapActions } from "vuex";

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: 'ConferenceListItem',
  props: {
    item: {
      type: Object,
      required: true,
    },
    checked: Boolean
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
    ...mapState(conferenceStore, ["conferenceDetail"]),
  },
  methods: {
    ...mapActions("conferenceStore", ["createRoom", "getConferenceCategories"]),
    enterConference() {
      this.$router.push({
        path: `conference/view/${this.item.id}`,
      });
    },
    transStr(str) {
      let transedStr = str.replaceAll('<b>', '')
      transedStr = transedStr.replaceAll('</b>', '')
      return transedStr
    },
    transUrl(url) {
      let transedUrl = url.replace('=m1&', '=m200&') 
      return transedUrl
    },
    replaceByDefault(e) {
      e.target.src = require('@/assets/thumbnail/thumbnail_default_img.jpg')
    }
  },
  data: function () {
    return {
      thumbnail_url: require('@/assets/thumbnail/thumbnail_default_img.jpg') // book cover url null   
    }
  }
}
</script>

<style scoped>
.card {
  height: 100%;
}
</style>